<template>
    <b-container fluid class="p-0">
      
    </b-container>
</template>

<script>
import AffiliateService from "@/services/resources/AffiliateService";
const service = AffiliateService.build();

export default {
  methods:{
    redirect(affiliate){

      let data = {
        id: affiliate
      };

      service
      .read(data)
      .then(response => {
        window.location.href = response.product.sales_page;
      })
      .catch(err => {
        console.log(err);
      });

    }
  },
  mounted(){
    var page = this.$route.params.page;
    document.cookie = 'affiliate=' + page + ';path=/'; // set cookie
    this.redirect(page);
  }
}
</script>
